<script>
import $ from 'jquery'
import { async } from 'q';
export default {
  name: 'about',

  data(){
    return{
        keyword:'',

        searchType:'a_name',
        year:'<=2019',
        month:'%',
        category:'%',
        orderWay:'playTimes desc',

        a_name:{},
        page:1
    }
  },
  
  methods:{
    
    sleep:function(ms){ 
      return new Promise(resolve => setTimeout(resolve, ms)) 
    },

    push:async function(ms){
      await this.sleep(ms)
      this.$router.push('detail')
    },

    getDetail:function(e){
      if(e.target.getAttribute('src')!=require("../assets/animeImg/0.png")){
      fetch(this.$store.state.backend + '/postName',{
        method:'POST',
        mode:'cors',
        body: JSON.stringify({'name': this.a_name[(this.page-1)*20 + parseInt(e.target.getAttribute('value'))]})
      })//.then(this.push(800))
      .then(this.$router.push('detail'))
      }
    },

    _trans:function(s){
        if(s==null) return '0'
        return s.replace(/[#:\/\\?*<>"| ()]/g,'_')
    },

    loadImg:function(){
      this.page = 1
      fetch(this.$store.state.backend + '/loadImg',{
        method:'POST',
        mode:'cors',
        body: JSON.stringify({
            'year':this.year,
            'month':this.month,
            'category':this.category,
            'orderWay':this.orderWay
          })
      }).then(res => res.json())
        .then(data => {
          //alert(data[1])
          this.a_name = data
          for(var i = 1; i<=20; i++){         
              document.getElementById('Img' + i.toString()).src=require("../assets/animeImg/" + this._trans(data[(this.page-1)*20+i]) + ".png")
          }

          var tag = document.getElementById('night-button-group')
          var code = ''
          for(var i = 1; i<=data[0]/20+1; i++){
            code += "<button class='night-pageButton' id="+i.toString()+" @click='changePage($event)' value="+ i.toString() +" style='margin:0 5px 0 10px'>"+ i.toString() +"</button>"
          }
          tag.innerHTML = code

          document.getElementById('1').style.backgroundColor='rgb(83,78,78)';

          var that = this
          $('#night-button-group').on("click", "button", function($event){
              that.page=$event.target.value
              for(var i = 1; i<=20; i++){          
                  document.getElementById('Img' + i.toString()).src=require("../assets/animeImg/" + that._trans(data[(that.page-1)*20+i]) + ".png")
              }
              for(var i = 1; i<=that.a_name[0]/20 + 1; i++){
                if(i.toString()==this.value) {
                  document.getElementById(i.toString()).style.backgroundColor='rgb(61,56,56)';
                }
                else document.getElementById(i.toString()).style.backgroundColor='white';
              }
              window.scrollTo(100,600)
          });

        })
        .catch(err =>{
          this.$message({
            type:'info',
            message:'网络连接失败'
          })
        })
    },

    changeYear:function(e){
      // alert(e.target.value)
      this.year = e.target.value
      this.loadImg()
    },

    changeMonth:function(e){
      // alert(e.target.value)
      this.month = e.target.value
      this.loadImg()
    },

    changeCategory:function(e){
      // alert(e.target.value)
      this.category = e.target.value
      //alert(this.category)
      this.loadImg()
      this.a_name[1]
    },

    changeOrder:function(e){
      // alert(e.target.value)
      this.orderWay = e.target.value
      this.loadImg()
    },

    changeSearch:function(e){
      this.searchType=e.target.value
    },

    getKeyword:function(e){
      this.keyword=e.target.value
    },

    searchBegin:function(e){
        fetch(this.$store.state.backend + '/search',{
        method:'POST',
        mode:'cors',
        body: JSON.stringify({
          'searchType':this.searchType,
          'keyword':this.keyword
        })
      }).then(res => res.json())
        .then(data => {
          this.a_name = data
          for(var i = 1; i<=20; i++){ 
              document.getElementById('Img' + i.toString()).src=require("../assets/animeImg/" + this._trans(data[(this.page-1)*20+i]) + ".png")
          }  
        })
        .catch(err =>{
          this.$message({
            type:'info',
            message:'网络连接失败'
          })
        })
    },
  },

  created(){
      this.loadImg();
  }
}
</script>