<template>
    <div id="maincontainer" :style="note">
        <div id="login-container">
        <div id="input-box">
            <div id="user-name-box">
                <span id="user">账 号：</span>
                <input name="user" id="userid" class="input" @change="getUsername($event)">
            </div>
            <div id="password-box">
                <span id="pw">密 码：</span>
                <input type="password" name="password" id="password" class="input" @change="getPassword($event)">
            </div>
            <button type="button" id="login-button" @click="submitForm(loginForm)">登陆</button>
            <button type="button" id="register-button" @click="register($event)">注册</button>
        </div>
        </div>
    </div>
</template>

<style>
#login-container{
    padding:80px;
    height: 120px;
    width: 240px;
    margin: 0 auto;
    position: absolute;
    top: 350px;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: #666 0px 0px 20px;
}
#userid{
    border-radius: 3px;
}
#password-box{
    margin-top: 10px;
}
#password{
    border-radius: 3px;
}
#login-button{
    border-radius: 3px;
    margin-top: 40px;
    padding: 5px;
    margin-right: 20px;
}
#register-button{
    border-radius: 3px;
    margin-top: 40px;
    padding: 5px;
}
</style>

<script>
// @ is an alias to /src
import {mapActions, mapState} from 'vuex'

export default {
  name: 'detail',
  data(){
    return {
      note: {
                backgroundImage: "url(" + require("@/assets/daymode.png") + ")",
                backgroundPosition: "center center",
                backgroundRepeat: "no-repeat",
                backgroundSize: "cover",
                width:"100%",
                height:"1000px",
                position:"relative",
                "box-sizing": 'border-box',
          },
      loginflag: false,
      loginForm: {
        username:'',
        password:'',
      },
      rules: {
        username:[
          { required: true, message:'请输入用户名', trigger:'blur'},
        ],
        password:[
          {required:true, message:'请输入密码', trigger:'blur'}
        ],
      },
      showLogin: false,
    }
  },
  mounted(){
    this.showLogin = true;
  },
  computed: {
    //...mapState(['isLogin'])
    isLogin: {
        get:function(){ 
          return this.$store.state.isLogin
        },
        set:function(newValue){
          this.$store.state.isLogin = newValue;
        }
    }
  },
  methods:{
    getUsername: function(e){
          this.loginForm.username = e.target.value;
    },
    getPassword: function(e){
          this.loginForm.password = e.target.value;
    },
    ...mapActions(['isLogin']),
    register:function(e){
      this.$router.push('/register')
    },
    submitForm(formName){
          if(this.$store.state.isLogin == 1){
              this.$message({
                type:'info',
                message:'已处于登录状态！'
              })
          }
          else if((this.loginForm.username == '页面是万伟霖做的' && this.loginForm.password == 'root')||(this.loginForm.username == '页面是罗旭川做的' && this.loginForm.password == 'root')){
                  this.$message({
                    type:'info',
                    message:'本地用户 验证通过'
                  })
                  localStorage.setItem('username',this.loginForm.username)
                  this.$store.dispatch('loginAction')
                  this.$router.push('/');          
          }
          else{
            fetch(this.$store.state.backend + '/loginCheck',{
              method:'POST',
              mode:'cors',
              body: JSON.stringify({
                  'username': this.loginForm.username,
                  'password': this.loginForm.password
              })
            }).then(res => res.text())
              .then(data => {
                  if(data == 'correct'){
                    this.$message({
                      type:'info',
                      message:'验证通过！'
                    })
                    localStorage.setItem('username',this.loginForm.username)
                    this.$store.dispatch('loginAction')
                    this.$router.push('/');
                  }
                  else{
                    this.$message({
                      type:'info',
                      message:'账号密码出错！'
                    })
                    console.log('账号密码出错');
                  }
              })
          }
          console.log('请求地址:' + '/');
    },
    check:function(){
      fetch(this.$store.state.backend + '/loginCheck',{
        method:'POST',
        mode:'cors',
        body: JSON.stringify({
            'username': this.loginForm.username,
            'password': this.loginForm.password
          })
      }).then(res => res.text())
        .then(data => {
          if(data=='corret'){
            this.checkResult = true
          }
        })
        .catch(err => {
            this.$message({
              type:'info',
              message:'网络连接失败'
            })
        })
    }
  }
}
</script>
